@import "partials/_variables";
// Small grid
//
// Columns, offsets, pushes, and pulls for the small device range, from phones
// to tablets.
//
// Note that `.col-sm-12` doesn't get floated on purposeâ€”there's no need since
// it's full-width.

@media only screen and (min-width: $st-screen-lg) and (max-width: $st-screen-lg-max) {   
  .product_list.grid {
    .product_list_item {
      &.first-in-line {
        clear: both;
      }
      &.first-line {
        border-top: none;
        padding-top: 0;
      }
    }
  }
  
  .pro_itemlist {
    .ajax_block_product {
      &.first-in-line {
        clear: both;
      }
      &.first-line {
        border-top: none;
      }
    }
  }
  .subcate_grid_view, .com_grid_view, .pro_itemlist, .blog_row_list, .blog_list_grid{
    .first-item-of-line{
      clear: both;
    }
  }
  

  
  .demo_product_pages{
    margin-left: 16px;
    margin-right: 16px;
  }
}


